___CSS_LOADER_EXPORT___.push([module.id, `section{
    display: block;
}
h3,header,ul{
    margin-bottom: 0.5rem;
    list-style-type: none;
}
#status{
   text-align: center;
}
h3{
   display: block;
   font-size: 1.17em;
   margin-block-start: 1em;
   margin-block-end: 1em;
   margin-inline-start: 0px;
   margin-inline-end: 0px;
   font-weight: bold;
}
h3 svg{
  width: 1.5rem;
  position: relative;
  top:5px;
  cursor: pointer;
}
h3 svg:hover{
   color:blue;
}
#checkbox{
   display: none;
}
.option{
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 10px;
   background:#eee;
   border-radius: 5px;
   position: relative;
}
.option::before{
   content: "";
   position: absolute;
   top: 5px;
   left:0;
   background-color:#fff;
   height: calc(100% - 10px);
   width: calc(50% - 10px);
   transform: translateX(5px);
   border-radius: inherit;
   box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
   transition: all 200ms;
}
input#type:checked ~ .option::before {
   left: 50%;
}
.option span{
   width: 50%;
   text-align: center;
   cursor: pointer;
   z-index: 2;
}

#transactionList{
   display: flex;
   flex-direction: column;
   list-style: none;
   height:3em;
   overflow-y: scroll;  
}
#transactionList li{
   display: flex;
   justify-content: space-between;
   align-items: flex-start;
   padding: 5px 10px;
   position: relative;
}
#transactionList li:hover{
   background: rgba(0, 0, 0, 0.1); 
}
.name{
   flex: 1;
}
.name h4{
  font-size: 1rem;
  font-size: 600;
  text-transform: capitalize;
}
.name p{
   font-size: 0.8rem;
   color:#555;
}
.amount{
   font-weight: 600;
}
.amount.income{
    color: yellowgreen;
}
.amount.expense{
   color: indianred;
}
.action svg{
   width: 36px;
   height: 36px;
   cursor: pointer;
}
.action{
   position: absolute;
   top:0;
   right:0;
   background-color: #f00;
   color:#fff;
   height:100%;
   width: 50px;
   display: grid;
   place-items: center;
   transform: scaleX(0);
   transform-origin: right;
   transition: all 300ms;
}


#transactionList li:hover .action{
  transform: scaleX(1);
}

.modal{
   display: none;
   position: fixed;
   z-index: 1000;
   left:0;
   top: 0;
   width: 100%;
   height: 100%;
   background-color: rgba(0, 0, 0, 0.5);
}
.modal-content{
   position: relative;
   margin:10% auto;
   padding: 20px;
   width: 80%;
   max-width: 600px;
   background: white;
   border-radius: 8px;
   box-shadow: 0 5px 15px rgba(0,0,0,0.5);
}
.close-btn{
   position: absolute;
   top:10px;
   right: 15px;
   font-size: 1.5rem;
   cursor: pointer;
   color: #333;
}
.modal h3{
  margin-bottom:1rem;
}

.modal-list{
   display: flex;
   justify-content: space-between;
   padding-bottom: 1rem;
}

















